import React,{Component} from "react";
// import { NavLink } from 'react-router-dom';
import HomeSelected from './img/tab-home-selected.png';
import img1 from './img/tab-commune-default.png';
import img2 from './img/tab-store-default.png';
import img3 from './img/tab-mine-default.png';
import { NavLink } from "react-router-dom/cjs/react-router-dom";
class Footer extends Component{
    render() {
      return (
        <div>
      <footer class="m-tabbar">
              <NavLink to="/home" className="tabbar-item" activeClassName="tabbar-active">
                  <span className="tabbar-icon">
                      <img src={HomeSelected}/>
                  </span>
                  <span className="tabbar-txt">首页</span>
              </NavLink>
              <NavLink to="/detail"  className="tabbar-item" activeClassName="tabbar-active">
                  <span className="tabbar-icon">
                      <img src={img1}/>
                  </span>
                  <span class="tabbar-txt">公社</span>
              </NavLink>
              <NavLink to="/follow"  className="tabbar-item" activeClassName="tabbar-active">
                  <span class="tabbar-icon">
                      <img src={img2}/>
                  </span>
                  <span class="tabbar-txt">杂货铺</span>
                  </NavLink>
             <NavLink to="/column"  className="tabbar-item" activeClassName="tabbar-active">
                  <span class="tabbar-icon">
                      <img src={img3}/>
                  </span>
                  <span class="tabbar-txt">我的</span>
              </NavLink>
          </footer>
        </div>
      );
    }
  }

  export default Footer;
